import React, { useState, useEffect } from 'react';
import '../styles/Location.css';
import { puzzles } from '../data/puzzles';
import { dialogues } from '../data/dialogues'; // Добавлен импорт

const Location = ({ location, onLocationChange, onDialogueStart, onPuzzleStart, gameState }) => {
  const availableActions = location.actions.filter((action) => {
    // Для перемещений
    if (action.type === 'move') {
      return gameState.availableLocations[action.target];
    }
  
    // Для головоломок
    if (action.type === 'puzzle') {
      const puzzle = puzzles[action.target];
      
      // Проверка общего условия головоломки
      if (puzzle.condition) {
        // Для строковых условий вида "quest['some_quest']"
        if (typeof puzzle.condition === 'string') {
          const matches = puzzle.condition.match(/(\w+)\[["']([^"']+)["']\]/);
          if (matches) {
            const [_, stateKey, propKey] = matches;
            if (!gameState[stateKey] || !gameState[stateKey][propKey]) {
              return false;
            }
          } else if (!gameState[puzzle.condition]) {
            return false;
          }
        }
        // Для функциональных условий
        else if (typeof puzzle.condition === 'function' && !puzzle.condition(gameState)) {
          return false;
        }
      }
      return true;
    }
  
    // Для диалогов
    if (action.type === 'dialogue') {
      const dialogue = dialogues[action.target];
      // Проверяем condition диалога, если он есть
      if (dialogue?.condition) {
        // Для строковых условий
        if (typeof dialogue.condition === 'string') {
          const matches = dialogue.condition.match(/(\w+)\[["']([^"']+)["']\]/);
          if (matches) {
            const [_, stateKey, propKey] = matches;
            if (!gameState[stateKey] || !gameState[stateKey][propKey]) {
              return false;
            }
          } else if (!gameState[dialogue.condition]) {
            return false;
          }
        }
        // Для функциональных условий
        else if (typeof dialogue.condition === 'function' && !dialogue.condition(gameState)) {
          return false;
        }
      }
      return true;
    }
  
    return true;
  });

  return (
    <div className="location">
      <h2 className='head'>{location.name}</h2>
      <img src={`/images/${location.image}`} alt={location.name} className='locationImg'/>
      <div className="actions">
        {availableActions.map((action) => {
          if (action.type === 'dialogue') {
            const dialogue = dialogues[action.target];
            if (dialogue?.condition && !gameState.dialog[action.target]) {
              return null;
            }
          }

          const imageSrc = `/images/${action.image}`;

          return (
            <button
              className="card"
              key={action.id}
              onClick={() => {
                if (action.type === 'move') {
                  onLocationChange(action.target);
                } else if (action.type === 'dialogue') {
                  onDialogueStart(action.target);
                } else if (action.type === 'puzzle') {
                  onPuzzleStart(action.target);
                }
              }}
            >
              <img src={imageSrc} alt={action.label} className='cardImg'/>
              <div className='cardName'>
                {action.label}
              </div>
            </button>
          );
        })}
      </div>
    </div>
  );
};

export default Location;