    text: 'О, вы тоже об этом?.. (вздыхает) Поверьте, между мной и хозяином ничего не было! Госпожа Габи говорила, что ждёт… специалиста. Вы ведь не родственница? Неужели госпожа специально наняла Вас для этого вопроса...',
