  itemDocuments: {
    key: "itemDocuments",
    character: "Старые документы",
    image: 'Isabelle_Duvall2.gif',
    text: [
      "Письмо 1 (датировано 100 лет назад): Дорогой брат, Я пишу тебе в отчаянии. То, что мы сделали, было ошибкой. Мы думали, что сможем контролировать силы, которые пробудили, но они оказались сильнее нас. Жертва, которую мы принесли, не принесла нам счастья. Напротив, она навлекла на нас проклятие. Я чувствую, что оно преследует наш род. Мы должны остановить это, пока не стало слишком поздно. Сожги все записи. Забудь о том, что было. Иначе это погубит нас всех.",
      "Запись из семейной хроники (50 лет назад): Сегодня ночью я видел её. Тень, которая бродит по коридорам. Она шепчет что-то на языке, которого я не понимаю. Я знаю, что это связано с тем, что сделали наши предки. Они принесли жертву, чтобы получить власть, но вместо этого навлекли на нас проклятие. Я пытался найти способ остановить это, но все мои попытки были тщетны. Единственное, что я могу сделать, — это спрятать правду. Пусть она умрёт вместе со мной.",
      "Ритуальный текст (без даты) Чтобы пробудить силу, которая спит в крови нашего рода, нужно принести жертву. Жертва должна быть добровольной, иначе ритуал не сработает. Кровь жертвы откроет врата, и сила перейдёт к тому, кто проведёт ритуал. Но будьте осторожны: если ритуал будет прерван, сила обернётся против вас. И тогда проклятие падёт на весь род.",
    ],
    audio: "/audio/itemDocuments.mp3",
    condition: null, // Доступен сразу
    options: [],
  },

