    text: "Я был в библиотеке. Изучал старые документы, пытался разобраться в истории этого поместья. Элиза разрешила мне доступ к архивам, за что я был ей благодарен. Я вышел только один раз, чтобы поговорить с ней, но она не открыла дверь. Я слышал, как она что-то бормотала... Звучало как заклинание. Я решил не мешать и вернулся в библиотеку.",
