import React, { useEffect, useRef } from 'react';
import '../styles/Dialogue.css';
import ReactAudioPlayer from 'react-audio-player';

const Dialogue = ({ dialogue, onClose, onSelectOption, gameState }) => {
  
  const audioRef = useRef(null);

  // Воспроизведение звука при открытии диалога
  useEffect(() => {
    if (dialogue?.audio && audioRef.current) {
      audioRef.current.src = dialogue.audio;

      const playAudio = async () => {
        try {
          await audioRef.current.play();
        } catch (error) {
          console.error("Ошибка воспроизведения звука:", error);
        }
      };

      playAudio();
    }

    // Остановка звука при закрытии диалога
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0; // Сбрасываем время воспроизведения
      }
    };
  }, [dialogue]);




  // Проверяем, что dialogue существует
  if (!dialogue) {
    return <div>Диалог не найден.</div>;
  }

  // Фильтруем варианты ответов по условиям
  const availableOptions = dialogue.options.filter((option) => {
    if (!option.condition) return true; // Если условие отсутствует, вариант доступен
    
    try {
      // Парсим условие типа 'items["Gaby_ring"]' или 'dialog["Gaby_a-01_2"]'
      const matches = option.condition.match(/(\w+)\["([^"]+)"\]/);
      if (matches) {
        const [_, stateKey, propKey] = matches;
        return gameState[stateKey] && gameState[stateKey][propKey];
      }
      
      // Для простых условий (если они есть)
      return gameState[option.condition];
    } catch (e) {
      console.error("Error evaluating condition:", option.condition, e);
      return false;
    }
  });

  // Проверяем, является ли text массивом
  const textArray = Array.isArray(dialogue.text) ? dialogue.text : [dialogue.text];

  return (
    <div className="dialogue-overlay">
      <div className="dialogue">
        <h2>{dialogue.character}</h2>
        {dialogue.type === 'item'? 
        <img className='dialogueImgItem' src={`/images/${dialogue.image}`}></img>:
        <div className='dialogueImg_conteiner'>
          <img className='dialogueImgItem' src={`/images/${dialogue.image}`}/>
        </div>}
        {console.log(dialogue.text)}
        {textArray.map((paragraph, index) => (
          <p className='dialogueP' key={index}>{paragraph}</p>
        ))}
        {availableOptions.length > 0 && (
          <div className="dialogue-options">
            {availableOptions.map((option, index) => (
              <button
                key={index}
                onClick={() => onSelectOption(option.next)}
              >
                {option.text}
              </button>
            ))}
          </div>
        )}
        {/*<button onClick={onClose}>Закрыть</button>*/}
        {availableOptions.length === 0 && (
        <button onClick={() => onClose(dialogue)}>Продолжить</button>
        )}
      </div>
      <audio ref={audioRef} style={{ display: 'none' }} />
    </div>
  );
};

export default Dialogue;