import React, { useState } from 'react';
import '../styles/Inventory.css';

const Inventory = ({ items }) => {
  const [selectedItem, setSelectedItem] = useState(null);

  // Группируем предметы по ключу и считаем количество
  const itemCounts = items.reduce((acc, item) => {
    acc[item.key] = (acc[item.key] || 0) + 1;
    return acc;
  }, {});

  // Получаем уникальные предметы
  const uniqueItems = [...new Set(items.map(item => item.key))]
    .map(key => items.find(item => item.key === key));

  const handleItemClick = (item) => {
    setSelectedItem(item);
  };

  const handleCloseModal = () => {
    setSelectedItem(null);
  };

  const renderDescription = (description) => {
    if (Array.isArray(description)) {
      return description.map((paragraph, index) => (
        <p key={index}>{paragraph}</p>
      ));
    }
    return <p>{description}</p>;
  };

  return (
    <>
      {selectedItem && (
        <div className="inventory-overlay">
          <div className="inventory-modal">
            <h2>{selectedItem.name}</h2>
            {selectedItem.stackable && (
              <p className="item-count-info">Количество: {itemCounts[selectedItem.key]}</p>
            )}
            {renderDescription(selectedItem.description)}
            <button onClick={handleCloseModal}>Закрыть</button>
          </div>
        </div>
      )}
      
      <div className="inventory">
        <h3>Инвентарь</h3>
        <div className="inventory-items">
          {uniqueItems.length > 0 ? (
            uniqueItems.map((item) => {
              const imageSrc = `/images/${item.image}`;
              return (
                <button
                  key={item.key}
                  className="inventory-item"
                  onClick={() => handleItemClick(item)}
                >
                  <p className='cardName'>{item.name}</p>
                  <img src={imageSrc} className='cardImg' alt={item.name} />
                  {item.stackable && itemCounts[item.key] > 1 && (
                    <span className="item-count">{itemCounts[item.key]}</span>
                  )}
                </button>
              );
            })
          ) : (
            <p>Инвентарь пуст.</p>
          )}
        </div>
      </div>
    </>
  );
};

export default Inventory;