import React, { useState } from "react";
import ReactDOM from "react-dom";
import Header from "./Header";
import Location from "./Location";
import Dialogue from "./Dialogue";
import Puzzle from "./Puzzle";
import Inventory from "./Inventory";
import { locations } from "../data/locations";
import { characters } from "../data/characters";
import { dialogues } from "../data/dialogues";
import { puzzles } from "../data/puzzles";
import { items } from "../data/items";
import "../styles/App.css";

const App = () => {
  // Начальное состояние
  const [currentLocation, setCurrentLocation] = useState("hall");
  const [activeDialogue, setActiveDialogue] = useState(null);
  const [activePuzzle, setActivePuzzle] = useState(null);
  const [inventory, setInventory] = useState([]);

  const [gameState, setGameState] = useState({
    quest: {
      Gabys_room__quest: true,
      library__quest: false,
    },
    inventory: {
      Gaby_ring: false,
    },
    items: {
      Gaby_ring: false,
    },
    dialog: {
      'Louise_a-01_1': true,
      'Gaby_a-01_1': true,
      'Augustine_a-01_1': true,
    },
    availableLocations: {
      hall: false,
      canteen: false,
      garden: false,
      library: false,
      Gabys_room: false,
    },
    dialogEliza: false,
    dialogAlibi: false,
    dialogOccult: false,
    dialogFamily: false,
    itemKey: true,
    itemDiary: true,
    itemGlass: false,
    needGlassOn: true,
    needGlass: false,
    puzzle__secret_box: false,
    ааavailableLocations: {
      canteen: true,
      kitchen: true,
      Marcels_bedroom: true,
      Sophies_room: true,
      Marcels_office: true,
      garden: true,
      Claras_room: true,
      library: true,
    },
    // Добавь другие условия по мере необходимости
  });

  // Данные текущей локации
  const locationData = locations[currentLocation];

  const handleLocationChange = (locationId) => {
    setCurrentLocation(locationId);
  };

  const handlePuzzleStart = (puzzleId) => {
    setActivePuzzle(puzzleId);
  };

  const addToInventory = (itemKey) => {
    const item = items[itemKey];
    if (!item) {
      console.error(`Предмет "${itemKey}" не найден`);
      return false;
    }
  
    // Для stackable предметов просто добавляем новый экземпляр
    if (item.stackable) {
      setInventory(prev => [...prev, item]);
      return true;
    }
  
    // Для не-stackable проверяем наличие
    if (inventory.some(i => i.key === itemKey)) {
      console.log(`Уже есть: ${item.name}`);
      return false;
    }
  
    setInventory(prev => [...prev, item]);
    return true;
  };

  // Обработчик выбора варианта ответа
  const handleSelectOption = (nextDialogue) => {
    if (nextDialogue) {
      setActiveDialogue(nextDialogue); // Переход к следующему диалогу
    } else {
      setActiveDialogue(null); // Завершение диалога
    }
  };

  const handleDialogueStart = (dialogueKey) => {

    const dialogue = dialogues[dialogueKey];
    if (dialogue?.condition && !gameState.dialog[dialogueKey]) {
      console.log(`Диалог ${dialogueKey} заблокирован. Условие не выполнено.`);
      return;
    }
    setActiveDialogue(dialogueKey); // Устанавливаем ключ диалога
  };

  // Функция для удаления предмета
  const removeFromInventory = (itemKey) => {
    setInventory(prev => prev.filter(item => item.key !== itemKey));
    setGameState((prevState) => ({
      ...prevState,
      inventory: {
        ...prevState.inventory,
        [itemKey] : false,
      },
    }));
  };

  const handleDialogueEnd = (dialogue) => {
    //ГЛАВА 1
    //После разговора с Луизой
    if (dialogue.key === "Louise_a-01_112" || dialogue.key === "Louise_a-01_1132" || dialogue.key === "Louise_a-01_1131" || dialogue.key === "Louise_a-01_1112" || dialogue.key === "Louise_a-01_11112" || dialogue.key === "Louise_a-01_1111112" || dialogue.key === "Louise_a-01_1111111" || dialogue.key === "Louise_a-01_1122112111") {
      setGameState((prevState) => ({
        ...prevState,
        dialog: {
          ...prevState.dialog,
          'Louise_a-01_1': false,
        },
      }));
      setCurrentLocation('canteen');
    }
    //Разговор с Габи
    if (dialogue.key === "Gaby_a-01_1111" || dialogue.key === "Gaby_a-01_1112111") {
      setGameState((prevState) => ({
        ...prevState,
        dialog: {
          ...prevState.dialog,
          'Gaby_a-01_1': false,
          'Gaby_a-01_2': true,
        },
      }));
      setGameState((prevState) => ({
        ...prevState,
        availableLocations: {
          ...prevState.availableLocations,
          canteen: true,
          garden: true,
          library: true,
          Gabys_room: true,
        },
      }));
      setGameState((prevState) => ({
        ...prevState,
        availableLocations: {
          ...prevState.availableLocations,
          canteen: true,
          garden: true,
          library: true,
          Gabys_room: true,
        },
      }));
    }
    //Разговор с Огюстиной
    if (dialogue.key === "Augustine_a-01_111111111" || dialogue.key === "Augustine_a-01_111111112" || dialogue.key === "Augustine_a-01_111121111" || dialogue.key === "Augustine_a-01_1122112111" || dialogue.key === "Augustine_a-01_11112121" || dialogue.key === "Augustine_a-01_112221" || dialogue.key === "Augustine_a-01_11221111" || dialogue.key === "Augustine_a-01_112121" || dialogue.key === "Augustine_a-01_1121111") {
      setGameState((prevState) => ({
        ...prevState,
        dialog: {
          ...prevState.dialog,
          'Augustine_a-01_1': false,
        },
      }));
      setGameState((prevState) => ({
        ...prevState,
        quest: {
          ...prevState.quest,
          'library__quest' : true,
        },
      }));
    }
    //передаем кольцо
    if (dialogue.key === "Gaby_a-01_21") {
      removeFromInventory("Gaby_ring"); // Удалить кольцо после передачи
    }






    //Новая тема окультизм после прочтения дневников
    if (dialogue.key === "itemDiary") {
      setGameState((prevState) => ({
        ...prevState,
        dialogOccult: true,
      }));
      addToInventory("diary");
      setGameState((prevState) => ({
        ...prevState,
        itemDiary: false,
      }));
    }

    //Первая встреча с Мари
    if (dialogue.key === "marie_acquaintance") {
      setGameState((prevState) => ({
        ...prevState,
        dialogEliza: true,
        dialogAlibi: true,
      }));
      setGameState((prevState) => ({
        ...prevState,
        availableLocations: {
          ...prevState.availableLocations,
          Marie: true,
          canteen: true,
          garden: true,
          library: true,
        },
      }));
    }

    //Новая тема о семье
    if (dialogue.key === "itemDocuments") {
      setGameState((prevState) => ({
        ...prevState,
        dialogFamily: true,
      }));
    }

    //Нужда в лупе
    if (dialogue.key === "inspection_Marie_1") {
      setGameState((prevState) => ({
        ...prevState,
        needGlass: true,
      }));
    }
        //Выдача лупы
        if (dialogue.key === "inspection_canteen_1") {
          setGameState((prevState) => ({
            ...prevState,
            needGlass: false,
            itemGlass: true,
            needGlassOn: false,
          }));
          addToInventory("glass");
        }


    //ВЫДАЧА КЛЮЧА
    if (dialogue.key === "marie_key") {
      addToInventory("key");
      setGameState((prevState) => ({
        ...prevState,
        itemKey: false,
      }));
      setGameState((prevState) => ({
        ...prevState,
        availableLocations: {
          ...prevState.availableLocations,
          Eliza: true,
        },
      }));
    }

    //Нашли тайник в библиотеке
    if (dialogue.key === "inspection_library_5") {
      setGameState((prevState) => ({
        ...prevState,
        puzzle__secret_box: true,
      }));
    }

    if (dialogue.key === "m") {
      // Добавляем предмет "Дневник Элизы"
      addToInventory("diary");
    }

    // Добавьте другие условия по мере необходимости
  };

  return (
    <div className="app">
      <Header />
      <div className="content">
        <Location
          location={locations[currentLocation]}
          onLocationChange={setCurrentLocation}
          onDialogueStart={setActiveDialogue}
          onPuzzleStart={setActivePuzzle}
          gameState={gameState}
        />
        {activeDialogue && (
          <Dialogue
            dialogue={dialogues[activeDialogue]}
            onClose={(dialogue) => {
              setActiveDialogue(null); // Закрываем диалог
              handleDialogueEnd(dialogue); // Обрабатываем завершение
            }}
            onSelectOption={handleSelectOption}
            gameState={gameState}
          />
        )}
{activePuzzle && (
  <Puzzle
    puzzle={puzzles[activePuzzle]}
    gameState={gameState}
    onClose={() => setActivePuzzle(null)}
    onSolve={({ reward, stateUpdates }) => {
      if (reward) {
        const added = addToInventory(reward);
        if (added) {
          // Применяем обновления состояния (если они есть)
          if (stateUpdates) {
            setGameState(prev => ({
              ...prev,
              ...stateUpdates
            }));
          }
          
          // Стандартное обновление для предмета (если не было в stateUpdates)
          if (!stateUpdates?.items?.[reward]) {
            setGameState(prev => ({
              ...prev,
              items: {...prev.items, [reward]: true}
            }));
          }
          
          alert(`Получен предмет: ${items[reward]?.name || reward}`);
        }
      }
      setActivePuzzle(null);
    }}
  />
)}
        <Inventory items={inventory} />
      </div>
    </div>
  );
};

export default App;
